// src/components/MenuItems.js
import React, { useState } from 'react';
import { List, ListItem, ListItemText, ListItemIcon, Collapse } from '@mui/material';
import DashboardIcon from '@mui/icons-material/Dashboard';
import SettingsIcon from '@mui/icons-material/Settings';
import EventNoteIcon from '@mui/icons-material/EventNote';
import BuildIcon from '@mui/icons-material/Build'; // Updated icon
import EmergencyIcon from '@mui/icons-material/Warning';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { useUser } from '@/contexts/UserContext';

import Link from 'next/link';

const MenuItems = () => {
  const [openSettings, setOpenSettings] = useState(false);
  const { user } = useUser();

  const handleSettingsClick = () => {
    setOpenSettings(!openSettings);
  };

  const menuItems = [
    // { text: 'Dashboard', icon: <DashboardIcon />, link: '/dashboard' },
    { text: 'Station Info', icon: <BuildIcon />, link: '/rms/station-info' },
    { text: 'Platforms-Devices', icon: <BuildIcon />, link: '/rms/platforms-devices' },
    { text: 'CAP Alerts', icon: <EmergencyIcon />, link: '/rms/cap-alerts' },
    { text: 'Event Logs', icon: <EventNoteIcon />, link: '/rms/event-logs' },
    {
      text: 'Settings',
      icon: <SettingsIcon />,
      items: user?.role === 'admin'
        ? [
            { text: 'RMS Settings', link: '/rms/settings' },
            { text: 'User Profile', link: '/profile' },
          ]
        : [
            { text: 'User Profile', link: '/profile' },
          ],
    },
  ];

  if (user?.role === 'admin') {
    menuItems.push(
      {
        text: 'Admin',
        icon: <SettingsIcon />,
        items: [
          { text: 'User Management', link: '/admin/users' },
        ],
      }
    );
  }

  return (
    <List>
      {menuItems.map((item, index) => (
        item.items ? (
          <React.Fragment key={index}>
            <ListItem button onClick={handleSettingsClick}>
              <ListItemIcon sx={{ color: 'lightgrey', minWidth: '35px' }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText primary={item.text} />
              {openSettings ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openSettings} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                {item.items.map((subItem, subIndex) => (
                  <Link href={subItem.link} key={subIndex} passHref>
                    <ListItem
                      button
                      sx={{ pl: 4, textDecoration: 'none', '&:hover': { textDecoration: 'none' } }}
                      component="a"
                    >
                      <ListItemText primary={subItem.text} />
                    </ListItem>
                  </Link>
                ))}
              </List>
            </Collapse>
          </React.Fragment>
        ) : (
          <Link href={item.link} key={index} passHref>
            <ListItem
              button
              sx={{ textDecoration: 'none', '&:hover': { textDecoration: 'none' } }}
              component="a"
            >
              <ListItemIcon sx={{ color: 'lightgrey', minWidth: '35px' }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText primary={item.text} />
            </ListItem>
          </Link>
        )
      ))}
    </List>
  );
}

export default MenuItems;
