'use client';

import React from 'react';
import { Box, Avatar, Typography, Divider, Toolbar } from '@mui/material';
import MenuItems from './MenuItems';
import { useUser } from '@/contexts/UserContext';

const Sidebar = () => {
  const { user } = useUser();

  return (
    <Box
      sx={{
        width: 260,
        height: '100vh',
        bgcolor: '#0A0F19',
        color: '#fff',
        display: 'flex',
        flexDirection: 'column',
        px: 3,
        py: 2,
        boxShadow: '2px 0 12px rgba(0, 0, 0, 0.4)',
      }}
    >
      <Toolbar />
      {user ? (
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            textAlign: 'center',
            mb: 2,
          }}
        >
          <Avatar
            src={user.profilePicture}
            alt={user.fullName}
            sx={{ width: 70, height: 70, mb: 1, border: '2px solid #00ED64' }}
          />
          <Typography variant="h6" fontWeight="bold" color="#00ED64">
            {user.fullName}
          </Typography>
          <Typography variant="body2" color="gray">
            {user.email}
          </Typography>
        </Box>
      ) : (
        <Typography variant="body2" align="center" color="gray">
          Loading...
        </Typography>
      )}

      <Divider sx={{ my: 2, borderColor: '#333' }} />

      <Box
        sx={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'flex-start',
        }}
      >
        <MenuItems user={user} />
      </Box>
    </Box>
  );
};

export default Sidebar;
